	}
	.navbar-toggle{
		font-size: 26px;
	}
	.upper-navbar{
		display: none;
	}
	.tablet-navbar{
		display: inline;
		top: 0;
		z-index: 100000;
		color: black;
		margin-left: 100px;
		height: 50px;
		font-size: 20px;
		position:relative; 
		top: 10px;
	}
}

/*DESKTOPS*/
@media screen and (min-width: 992px) {
	.sm-show{
		display: none;
	}
	.sm-screen{
		display: none;
		visibility: hidden;
	}
}

/*Colapse navbar at at 922px*/
@media (max-width: 992px) {
    .navbar-header {
        float: none;
    }
    .navbar-left,.navbar-right {
        float: none !important;
    }
    .navbar-toggle {
        display: block;
    }
    .navbar-collapse {
        border-top: 1px solid transparent;
        box-shadow: inset 0 1px 0 rgba(255,255,255,0.1);
    }
    .navbar-fixed-top {
		top: 0;
		border-width: 0 0 1px;
	}
    .navbar-collapse.collapse {
        display: none!important;
    }
    .navbar-nav {
        float: none!important;
		margin-top: 7.5px;
	}
	.navbar-nav>li {
        float: none;
    }
    .navbar-nav>li>a {
        padding-top: 10px;
        padding-bottom: 10px;
    }
    .collapse.in{
  		display:block !important;
	}
}
